.circles {
  width: 75vw;
  max-width: 960px;
  padding: 9px;
  box-sizing: border-box;
}

.circles-header {
  margin-bottom: 12px;
}

.circles-header__list {
  display: flex;
  align-items: center;
  padding: 7px 11px;
  background: $menu-list-bg;
  box-shadow: 0px 2px 10px rgba(0, 0, 0, 0.12);
  border-radius: 4px;
}

.circles-header__editor {
  background: #f8f9fc;
  box-shadow: 0px 2px 10px rgba(0, 0, 0, 0.12);
  border-radius: 4px;
  display: block;
  min-width: 170px;
  padding: 11px 4.5px;
  position: relative;
  transition: $default-menu-transition;

  &::before {
    @include defaul-pseudo-element(20px);
    background: url("./../../public/img/Profile.svg");
    top: 10px;
    left: 10px;
  }

  &:hover {
    scale: 1.07;
  }
}
